textarea {
    width: 100%;
}

.component {
    background-color: #ffb703;
    height: 100vh;
    color: #023047;
}

.btn {
    color: #023047;
    font-weight: bolder;
    background-color: #219ebc;
    outline: 1px solid #219ebc;
}

.navbar {
    border: 1px solid black;

    background-color: #fb8500;
}

.navbar a {
    color: #6a040f;
    font-size: large;
    font-weight: bolder;
}

.prev {
    background-color: #ffb703;
    /* border: 1px solid black; */
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
}

.prev p {
    padding: 10px;
    font-size: 20px;
    font-weight: bolder;
    display: flex;
    flex-wrap: wrap;
}

.navDark {
    background-color: #212529;
    color: white;
}

.box {
    /* border: 1px solid black; */
    padding: 0 100px;
}

.Dark {
    background-color: #292929;
    color: white;
}

.darkbutt {
    background-color: #C38FFF;
    color: #281D34;
}

.darkArea {
    background-color: #292929;
    color: white;
    border: 1px solid #C38FFF;
    outline: 1px solid #C38FFF;
}